@import "../mixins";
@import "../susy";

.edit_article {
    cursor: pointer;
    background: rgb(255, 165, 0);
    @include border-radius(4px);
    padding: 5px;
    &:hover {
        @include box-shadow(0 0 3px rgba(0,0,0,1));
    }
}

.desactive_article {
    cursor: pointer;
    background: rgb(217, 255, 0);
    @include border-radius(4px);
    padding: 5px;
    margin-left: 4px;
    &:hover {
        @include box-shadow(0 0 3px rgba(0,0,0,1));
    }
}

.delete_article {
    cursor: pointer;
    background: rgb(255, 0, 0);
    @include border-radius(4px);
    padding: 5px;
    margin-left: 4px;
    &:hover {
        @include box-shadow(0 0 3px rgba(0,0,0,1));
    }
}

.add_article {
    text-decoration: none;
}

